module.exports = "const Menu = ({ role }) => (\n  <Dropdown.Menu flip>\n    {({ show, onClose, props }) => {\n      return (\n        <div\n          {...props}\n          role={role}\n          className={css`\n            display: ${show ? 'flex' : 'none'};\n            min-width: 150px;\n            position: absolute;\n            flex-direction: column;\n            border: 1px solid #e5e5e5;\n            background-color: white;\n            box-shadow: 0 5px 15px rgba(0, 0, 0, 0.5);\n            padding: 20px;\n          `}\n        >\n          <button onClick={onClose} style={{ textAlign: 'left' }}>\n            Item 1\n          </button>\n          <button onClick={onClose} style={{ textAlign: 'left' }}>\n            Item 2\n          </button>\n        </div>\n      );\n    }}\n  </Dropdown.Menu>\n);\n\nconst Toggle = ({ id, children }) => (\n  <Dropdown.Toggle>\n    {({ toggle, show, props }) => (\n      <Button id={id} {...props} onClick={toggle}>\n        {children}\n      </Button>\n    )}\n  </Dropdown.Toggle>\n);\n\nconst DropdownButton = ({ show, onToggle, drop, alignEnd, title, role }) => (\n  <Dropdown\n    show={show}\n    onToggle={onToggle}\n    drop={drop}\n    alignEnd={alignEnd}\n    itemSelector=\"button:not(:disabled)\"\n  >\n    {({ props }) => (\n      <div\n        {...props}\n        className={css`\n          position: relative;\n          display: inline-block;\n        `}\n      >\n        <Toggle id=\"example-toggle\">{title}</Toggle>\n        <Menu role={role} />\n      </div>\n    )}\n  </Dropdown>\n);\n\nclass DropdownExample extends React.Component {\n  constructor(...args) {\n    super(...args);\n    this.state = { show: false };\n  }\n\n  render() {\n    const { show } = this.state;\n    return (\n      <div className=\"dropdown-example\">\n        <DropdownButton\n          show={show}\n          onToggle={show => this.setState({ show })}\n          title={`${show ? 'Close' : 'Open'} Dropdown`}\n        />\n        <DropdownButton alignEnd title=\"Align right\" />\n\n        <DropdownButton drop=\"up\" title=\"Drop up\" />\n        <DropdownButton role=\"menu\" title=\"Role 'menu'\" />\n      </div>\n    );\n  }\n}\n\nrender(<DropdownExample />);\n"