module.exports = "// Styles mostly from Bootstrap.\nconst styles = {\n  tooltip: {\n    position: 'absolute',\n    padding: '0 5px'\n    // transition: 'all 0.2s ease-out 0.2s',\n  },\n\n  inner: {\n    padding: '3px 8px',\n    color: '#fff',\n    textAlign: 'center',\n    borderRadius: 3,\n    backgroundColor: '#000',\n    opacity: 0.75\n  },\n\n  arrow: {\n    position: 'absolute',\n    width: 0,\n    height: 0,\n    borderStyle: 'solid',\n    opacity: 0.75\n  }\n};\n\nconst placementStyles = {\n  left: {\n    tooltip: {\n      marginLeft: -3,\n      padding: '0 5px'\n    },\n\n    arrow: {\n      right: 0,\n      borderWidth: '5px 0 5px 5px',\n      borderColor: 'transparent transparent transparent #000'\n    }\n  },\n\n  right: {\n    tooltip: {\n      marginLeft: 3,\n      padding: '0 5px'\n    },\n\n    arrow: {\n      left: 0,\n      borderWidth: '5px 5px 5px 0',\n      borderColor: 'transparent #232323 transparent transparent'\n    }\n  },\n\n  top: {\n    tooltip: {\n      marginTop: -3,\n      padding: '5px 0'\n    },\n\n    arrow: {\n      bottom: 0,\n      borderWidth: '5px 5px 0',\n      borderColor: '#232323 transparent transparent transparent'\n    }\n  },\n\n  bottom: {\n    tooltip: {\n      marginBottom: 3,\n      padding: '5px 0'\n    },\n\n    arrow: {\n      top: 0,\n      borderWidth: '0 5px 5px',\n      borderColor: 'transparent transparent #232323 transparent'\n    }\n  }\n};\n\nconst PLACEMENTS = ['left', 'top', 'right', 'bottom'];\n\nclass OverlayExample extends React.Component {\n  constructor(props, context) {\n    super(props, context);\n\n    this.state = {\n      show: false,\n      placement: null\n    };\n\n    this.onClick = () => {\n      const { placement } = this.state;\n      const nextPlacement = PLACEMENTS[PLACEMENTS.indexOf(placement) + 1];\n\n      return this.setState({\n        show: !!nextPlacement,\n        placement: nextPlacement\n      });\n    };\n  }\n\n  render() {\n    const { show, placement } = this.state;\n\n    return (\n      <div className=\"overlay-example\">\n        <Button\n          bsStyle=\"primary\"\n          id=\"overlay-toggle\"\n          ref={c => {\n            this.target = c && ReactDOM.findDOMNode(c);\n          }}\n          onClick={this.onClick}\n        >\n          I am an Overlay target\n        </Button>\n        <p>Keep clicking to see the placement change.</p>\n\n        <Overlay\n          show={show}\n          onHide={() => this.setState({ show: false })}\n          placement={placement}\n          container={this}\n          target={() => this.target}\n        >\n          {({ props, arrowProps, placement }) => {\n            const placementStyle = placementStyles[placement];\n            return (\n              <div\n                {...props}\n                style={{\n                  ...styles.tooltip,\n                  ...placementStyle.tooltip,\n                  ...props.style\n                }}\n              >\n                <div\n                  {...arrowProps}\n                  style={{\n                    ...styles.arrow,\n                    ...placementStyle.arrow,\n                    ...arrowProps.style\n                  }}\n                />\n                <div style={{ ...styles.inner }}>\n                  I&rsquo;m placed to the <strong>{placement}</strong>\n                </div>\n              </div>\n            );\n          }}\n        </Overlay>\n      </div>\n    );\n  }\n}\n\nrender(<OverlayExample />);\n"